<template>
  <div class="m-geo">
    <div class="position">
      <i class="el-icon-location" />
      {{ $store.state.position || ''}}
      <router-link class="changeCity" :to="{ name: 'changeCity' }"></router-link>[
      <a href="/changeCity"></a>
      <a href="/changeCity"></a>
      <a href="/changeCity"></a> ]
    </div>
    <div class="m-user" v-if="!$store.state.userName">
      <router-link class="login" :to="{name: 'login'}"></router-link>
      <router-link class="register" :to="{name: 'register'}"></router-link>
    </div>
    <div class="m-user" v-if="$store.state.userName">
      <router-link class="login" :to="{name: 'index'}">Hi,{{$store.state.userName}}</router-link>
      <router-link class="login" :to="{name: 'login'}"></router-link>
    </div>
    <tip class="posi" :tip="change" :dir="dir" />
  </div>
</template>

<script>
import api from "@/api/index.js";
import tip from "@/components/tip.vue";
export default {
  data() {
    return {
      change: "!!!",
      dir: {
        up: true
      }
    };
  },
  // created() {
  //   api.getLocation().then(res => {
  //     this.$store.dispatch("setPosition", res.data.data);
  //     console.log(res.data.data);
  //   });
  // },
  components: {
    tip
  }
};
</script>

<style lang="scss">
@import "@/assets/css/public/header/topbar.scss";
.posi {
  left: 30px;
}
</style>



